@media screen and (max-width: 900px) {
    .main {
        display: inline-block;
        box-sizing: border-box;
        width: 100%;
        margin-left: 10px;
        margin-right: 10px;
        overflow: hidden;
    }
    .article {
        text-align: left;
        width: 90%;
        box-sizing: border-box;
        margin-left: 5%;
        margin-right: 5%;
        margin-bottom: 30px;
    }
    /* 記事部分 */
    .container {
        margin-bottom: 40px;
    }
    figcaption {
        margin-top: 10px;
        color: #776;
        font-size: 5px;
    }
    /* 文字列 */
    p {
        color: #776;
    }
    /* 端末画像+出典 */
    .figure {
        margin: 0;
        padding: 0;
        overflow: hidden;
        position: relative;
    }
    /* 全体 */
    .flex {
        box-sizing: border-box;
        display: flex;
        /* 横並び */
        width: 100vw;
        /* width指定しないと左右中央に配置できない */
        margin-bottom: 40px;
    }
    .title {
        box-sizing: border-box;
        border-bottom: 5px solid #c2e4cb;
        margin-top: 40px;
        margin-bottom: 40px;
        text-align: left;
    }
}

@media screen and (min-width: 901px) {
    .main {
        display: inline-block;
        box-sizing: border-box;
        width: 100%;
        margin-left: 10px;
        margin-right: 10px;
        overflow: hidden;
    }
    .article {
        text-align: left;
        width: 90%;
        box-sizing: border-box;
        margin-left: 5%;
        margin-right: 5%;
        margin-bottom: 30px;
    }
    /* 記事部分 */
    .container {
        margin-bottom: 40px;
    }
    .figcaption {
        margin-top: 10px;
        color: #776;
        font-size: 12px;
        width: 300px
    }
    /* 文字列 */
    p {
        color: #776;
    }
    /* 端末画像+出典 */

    /* 全体 */
    .flex {
        box-sizing: border-box;
        display: flex;
        /* 横並び */
        width: 100vw;
        /* width指定しないと左右中央に配置できない */
        margin-bottom: 40px;
    }
    .title {
        box-sizing: border-box;
        border-bottom: 5px solid #c2e4cb;
        margin-top: 40px;
        margin-bottom: 40px;
        text-align: left;
    }
}


