@import url(http://fonts.googleapis.com/earlyaccess/notosansjp.css);
main {
    text-align: center;
    font-family: 'Noto Sans JP';
}

.App {
    text-align: center;
    font-family: 'Noto Sans JP';
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

.weight100 {
    font-weight: 100
}

.weight300 {
    font-weight: 300
}

.weight400 {
    font-weight: 400
}

.weight500 {
    font-weight: 500
}

.weight600 {
    font-weight: 600
}

.weight700 {
    font-weight: 700
}

.weight900 {
    font-weight: 900
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}